
      /* invalid screen styling */

      .invalidScreen{
        background-color:  #1eb2a6;
        color:white;
        border-radius: 10px;
        padding: 15px;
        box-shadow: 2px 2px 8px 2px black;
        margin-left: 120px;
        margin-top: 30px;
        animation-name: button;
        animation-duration: 1s;
        animation-iteration-count: infinite;
        animation-duration: alternate-reverse;
      }
     
      @keyframes button{
        from {
         background-color: rgb(243, 234, 234);
         color:black
        }
        to {
         background-color: rgb(247, 42, 52);
         color: white
        }
      }
     
     /* -------------------------------------------------- */
     
                         
     @import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");
     * {
       padding: 0;
       margin: 0;
       box-sizing: border-box;
     }
     body {
       font-family: "Roboto", sans-serif;
       background-color: #f8f8f8;
     }
     
     /*------------global-----------*/
     .container {
       max-width: 85%;
       margin: auto;
     }
     .flexSB {
       display: flex;
       justify-content: space-between;
     }
     .icon {
       width: 40px;
       height: 40px;
       line-height: 40px;
       border-radius: 50%;
       text-align: center;
       background-color: rgba(255, 255, 255, 0.3);
       color: #1eb2a6;
       transition: 0.5s;
     }
     .icon:hover {
       cursor: pointer;
       background-color: #1eb2a6;
       color: #fff;
     }
     a {
       text-decoration: none;
     }
     li {
       list-style-type: none;
     }
     .row {
       width: 50%;
     }
     button {
       padding: 17px 30px;
       background-color: #fff;
       color: #1eb2a6;
       font-weight: 600;
       border: none;
       margin: 30px 10px 0 0;
       border-radius: 3px;
       cursor: pointer;
       box-shadow: 0 24px 36px -11px rgb(0 0 0 / 9%);
       transition: 0.5s;
     }
     button i {
       margin-left: 20px;
     }
     button:hover {
       box-shadow: none;
     }
     .primary-btn {
       background-color: #1eb2a6;
       color: #fff;
     }
     
     /*------------global-----------*/
     
     #heading {
       text-align: center;
       padding: 40px 0;
     }
     #heading h3 {
       font-weight: 600;
       letter-spacing: 1px;
       color: #1eb2a6;
       text-transform: uppercase;
     }
     #heading h1 {
       font-size: 45px;
       margin: 20px 0;
       text-transform: capitalize;
     }
     p {
       line-height: 30px;
       font-size: 18px;
     }
     
     .back {
       background-image: url("../public/images/back.webp");
       background-size: cover;
       background-attachment: fixed;
       position: absolute;
       top: 0;
       left: 0;
       z-index: -1;
       width: 100%;
       height: 150vh;
       padding-top: 30%;
       padding-right: 50px;
       color: #fff;
       text-align: center;
     }
     .back h1 {
       font-size: 100px;
       font-weight: 400;
     }
     .back h2 {
       font-weight: 500;
       font-size: 17px;
       text-transform: uppercase;
     }
     .margin {
       margin-top: 40.3%;
     }
     .grid {
       display: grid;
       grid-template-columns: repeat(4, 1fr);
       grid-gap: 30px;
     }
     .flex {
       display: flex; 
     }
     .grid2{
       display:grid;
       grid-template-columns: repeat(2, 1fr);
       grid-gap: 30px;
       margin-top: 40em;
     }
     
     .outline-btn {
       margin: 0;
       box-shadow: none;
       border: 2px solid #1eb2a6;
       width: 100%;
       transition: 0.5s;
     }
     .outline-btn:hover {
       background-color: #1eb2a6;
       color: #fff;
     }
     .grid3{
       display:grid;
       grid-template-columns: repeat(3, 1fr);
       grid-gap: 10px;
     }
     .padding {
       padding: 80px 0;
     }
     .shadow{
       box-shadow: 0 5px 25px -2px rgb(0 0 0 /6%);
       background-color: #fff;
     }
     
     @media screen and (max-width:768px){
       .grid3,
       .grid2,
       .grid {
         grid-template-columns: repeat(2 , 1fr);
      }
       .back{
         background-position: center;
         padding-top: 50% !important;
         height: 180vh;
       }
       .margin{
         margin-top: 80%;
       }
     }