.aboutHome {
  height: auto;
}
.aboutHome #heading {
  text-align: left;
  padding: 0;
}
.aboutHome .left img {
  width: 100%;
  height: 52em;
  position: relative;
  top: 19.7em;
  object-fit: cover;
}
#heading{
  margin-top:280px;
}
.aboutHome .right {
  padding: 50px 20px;
}
.aboutHome .items {
  margin-top: 20px;
}
.aboutHome .item {
  background-color: #fff;
  margin-top: 20px;
  padding: 20px;
  transition: 0.5s;
}
.aboutHome .img {
  width: 200px;
}
.aboutHome img {
  width: 70px;
  height: 70px;
}
.aboutHome .item h2 {
  font-size: 20px;
  margin-bottom: 15px;
}
.aboutHome .item p {
  color: #999999;
}
.aboutHome .item:hover {
  background-color: #1eb2a6;
  color: #fff;
  cursor: pointer;
  box-shadow: 0 5px 25px -2px rgb(0 0 0 / 6%);
}
.aboutHome .item:hover p {
  color: #fff;
}
/*----------awrapper------------*/
.awrapper {
  background-image: url("https://img.freepik.com/premium-photo/back-school-background-with-school-supplies_343732-13020.jpg?size=626&ext=jpg&ga=GA1.1.1141335507.1717891200&semt=ais_user");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 40vh;
  color: #fff;
  position: relative;
  top: -20px;
}
.awrapper .box {
  padding: 70px 0;
}
.awrapper img {
  margin-right: 30px;
}
.awrapper h1 {
  font-size: 50px;
}
.awrapper h3 {
  font-size: 20px;
  font-weight: 500;
}
/*----------awrapper------------*/
@media screen and (max-width: 768px) {
  .aboutHome {
    margin-top: 10%;
  }
  .aboutHome .container {
    flex-direction: column-reverse;
  }
  .aboutHome .row {
    width: 100%;
    margin-top: 90px;
  }
  .aboutHome .left {
    position: relative;
    bottom: 250px;
  }
  .awrapper {
    height: 50vh;
  }
  .awrapper img{
    width: 40px;
    margin-right:3px;
  }
  .awrapper h1{
    font-size: 15px;
  }
  .awrapper {
  height: 30vh;
  padding-bottom: 10px;

  }
  
}